
.main{
  margin: 5%;
  background-color: #a3c1ec;
  border-bottom: solid 5px #628bfc;
}

.aboutHeader h1{
  margin: 0%;
  text-align: center;
  background-color: #628bfc;
}

.container{
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
}

.keyimage{
  flex: 1;
  text-align: center;
  max-width: 100%;
  max-height: 100%;
}

.image img{
  max-height: 100%;
  display: inline-block;
  width: 200px;
  height: 200px;
  position: relative;
  overflow: hidden;
  border-radius: 50%;
  margin-top: 15%;
}



hr.style {
  border: 0;
  height: 1px;
  background: #333;
  background-image: -webkit-linear-gradient(left, #ccc, #333, #ccc);
  background-image:    -moz-linear-gradient(left, #ccc, #333, #ccc);
  background-image:     -ms-linear-gradient(left, #ccc, #333, #ccc);
  background-image:      -o-linear-gradient(left, #ccc, #333, #ccc);
}
