.about {
  flex: 6;
  max-width: 87%;
  padding: 20px;
}

.about p {
  font-size: 20px;
  padding-bottom: 3%;
}

.about h1 {
  margin-bottom: 2px;
  display: inline-block;
  margin-top: 0px;
  padding-right: 10px;
  font-size: 3.5vmax;
}


.iconLink{
  text-align: left;
  display: inline;
}

.iconLink *{
  padding: 0 2px 0 2px;
  color: black;
}

svg :hover{
  color: #fff;
}


.techUse{
  text-transform:  center;
  max-height: 15%;
}

.logoImage img{
